// Common integration harness tweaks

// Reduce noise on CI
if (process.env.CI) {
  const drop = (..._args: any[]) => {};
  console.debug = drop;
}

// Provide a default base URL for tests that expect it
process.env.TEST_BASE_URL = process.env.TEST_BASE_URL || 'http://localhost:4000';

